@import "project.less";
@import "smart-grid.less";


.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}


/* Defaults */
.reset();

body {
 .reg();
	font-size: 15px;
	color: @text;
	min-width: 300px;
}

button {
			&:focus {
						outline: none
			}
}

input, {
	&:focus {
		outline: none; 
	}
}

input[type=submit],
button {
	cursor: pointer;
}

a {
		color: @color_link;
		text-decoration: none;
		&:hover {
				color: darken(@color_link, 15%);
		}
}

.wrapper {
	.wrapper();
}

.clearfix {
		display: block;
		clear: both;
}


/* Оставить заявку */ 
.leftOrder {
		display: block;
		width: 100%;
		height: 50px;
		line-height: 50px;
		text-align: center;
		border: none;
		border-radius: 5px;
		font-weight: 500;
		font-size: 18px;
		

		&_orange {
				background: @orange;
				color: @white;
		}

		&_white {
				background: @white;
				color: @text;
				border: 1px solid #FF8A00;
		}
}


main,
section,
footer {
			max-width: 1440px;
			margin: 0 auto;
}

section {
	padding: 50px 0;


	.xs-block({
				padding: 30px 0;
	});

	.section-title {
				margin-bottom: 50px;

				h2 {
					font-size: 48px;
					font-weight: 300;
					text-align: center;

					.md-block({
							font-size: 38px;
					});

					.sm-block({
							font-size: 32px;
					});
				}


	}

	.section-title_lite {
				margin-bottom: 30px;

				h2 {
					font-size: 48px;
					font-weight: 300;
					text-align: center;
					color: @white;

					.md-block({
								font-size: 38px;
						});

					.sm-block({
								font-size: 32px;
						});
				}
	}

}



/* Даю согласие */ 
.formCheck {

		margin-top: 20px;
		margin-left: -25px;
	 &-input {
	 	display: none;

	 	&:checked + label::before {
						background: url('../img/main/check-icon.png') center center no-repeat;
			}
	 }

	 label {
	 		padding-left: 5px;
	 		font-size: 12px;

	 		&:before {
	 					content: '';
	 					display: inline-block;
	 					width: 16px;
	 					height: 16px;
	 					margin-right: 10px;
	 					border: .5px solid #DBDBDB;
	 					border-radius: 2px;
	 					background: transparent;
	 					cursor: pointer;
	 					vertical-align: middle;
	 					margin-top: -3px;
	 		}
	 }
}







/* ===================== Blocks ========================= */

.sliderMain {
			max-width: 1440px;
			margin: 0 auto;
			box-sizing: border-box;
}

/* ======  Main  ======= */
.main {
				// background: url('../img/main/main-bg.jpg') no-repeat;
				// background-size: cover;
				padding: 37px 0;
		
				.xs-block({
								padding: 20px 0;
					});

				.header {
							.row-flex;

							.logo {
										.col;
										.size(3);

										display: flex;
										align-items: center;

										.md-block({
													.size(4);
											});

										.xs-block({
													.size(6);
											});

										.xxs-block({
													.size(6);
											});

							}

							.address {
										.col;
										.size(4);

										display: flex;
										justify-content: flex-end;
										align-items: center;

										.md-block({
													.size(4);
											});

										.xs-block({
													display: none;
											});

									&-icon {
											margin-right: 10px;
									}

									&-text {
											color: @white;
											font-size: 14px;

									}
							}

							.contact {
									.col;
									.size(5);
									
									display: flex;
									flex-wrap: wrap;
								 justify-content: flex-end;
								 align-items: center;

								 .md-block({
								 			.size(4);
								 	});

								 .xs-block({
								 			.size(6);
								 			flex-wrap: wrap;
								 	});

								 .xxs-block({
								 			.size(6);
								 	});

								 .contactLeft {
								 	margin-right: 20px;

								 	.md-block({
								 				margin-bottom: 10px;
								 		});

								 		&-phone {
								 			font-size: 24px;
								 			font-weight: 500;

								 			.md-block({
								 						font-size: 18px;
								 				});

								 			.xxs-block({
								 						font-size: 14px;
								 				});
								 		}

								 		&-email {
								 			font-size: 14px;
								 			letter-spacing: 0.05em;
								 			text-align: right;

								 			.xxs-block({
								 						font-size: 12px;
								 				});
								 		}
								 }

									.contactRight {
												display: flex;
												align-items: center;
												justify-content: center;

												&-link {
													 display: block;
													 font-size: 14px;
													 text-transform: uppercase;
													 line-height: 40px;
														width: 160px;
														height: 40px;
														text-align: center;
														border: 1px solid @white;
														border-radius: 5px;

														.xxs-block({
																	display: none;
															});
												}
									}

									a {
										color: @white;
									}
							}
						
				}

				.mainBlock {
					padding: 95px 0 50px 0;
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;

					.xs-block({
								padding: 60px 0 20px 0;
						});
							
							&-title {
								font-size: 48px;
								color: @white;
								flex-basis: 560px;
								cursor: default;

								.md-block({
											font-size: 40px;
											margin: 0 auto 60px;
											text-align: center;
									});

								.xs-block({
											font-size: 32px;
											margin: 0 auto 60px;
									});
							}
				}



				/* Форма */ 
				.bLockform {
							background: #00B6CF;
							padding: 25px 40px 30px 30px;
							text-align: center;
							color: @white;

							.md-block({
											margin: 0 auto;
								});

							&-title {
									font-size: 36px;
									margin-bottom: 3px;
							}
							&-subtitle {
								 font-size: 14px;
								 margin-bottom: 22px;
							}
				}


				.form {

							&-input {
								margin-bottom: 10px;

										input {
											 width: 100%;
											 height: 50px;
											 background: transparent;
											 font-size: 18px;
											 font-weight: 300;
											 color: #EAF7FF;
											 text-align: center;
											 border: 1px solid #EAF7FF;
											 border-radius: 2px;

											 &::-webkit-input-placeholder {
											 		font-size: 18px;
											 		color: #EAF7FF;
											 		opacity: 0.5;
											 	}

											 &::-moz-placeholder {
											 		font-size: 18px;
											 		color: #EAF7FF;
											 		opacity: 0.5;
											 	}
										}
							}

						&-send {
								margin-top: 15px;

								&:hover {
										color: darken(@color_link, 15%);
								}

						}

						
				}

}




/* ======  Services  ======= */

.services {
		background: #E5E5E5;

			.servicesWrap {
						.row-flex();
						margin-bottom: 40px;
						
						.md-block({
										justify-content: center;
							});

						.servicesBlock {
										.col;
										.size(3);

										.lg-block({
													.size(4);
													margin-bottom: 20px;
											});

										.md-block({
													.size(6);
													text-align: center;
											});

										.sm-block({
													.size(10);
													text-align: center;
											});

										.xs-block({
													.size(12);
													text-align: center;
											});

										padding: 20px;
										box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
										background: @white;
										display: flex;
										flex-direction: column;

										&-title {
											font-size: 20px;
											text-align: center;
											line-height: 1.1;
											margin-bottom: 10px;

											span {
												display: block;
											}
										}

										&-img {
											margin-bottom: 10px;
										}

									 &-list {
									 	list-style-image: url('../img/services/advant-icon.png') no-repeat;
									 	line-height: 24px;
									 	margin-bottom: 10px;
									 	text-align: left;
									 			
									 	.md-block({
									 			margin: 0 auto;
									 		});

									 	li {
									 			padding-bottom: 20px;

									 			&::before {
									 				content: '';
									 				display: inline-block;
									 				width: 16px;
									 				height: 12px;
									 				background: url(../img/services/advant-icon.png) no-repeat;
									 				padding-right: 20px;
									 			}
									 	}
									 }

									 a.leftOrder {
									 		margin-top: auto;
									 }
						}

			}

			.servicesAdd {
						max-width: 85%;
						margin: 40px auto 40px;

					.sm-block({
									max-width: 100%;
						});

						.servicesAddBlock {
										background: @white;
										box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
										box-sizing: border-box;
										padding: 20px;

										.sm-block({
													text-align: center;
											});
										
										&-img {
												float: left;
												margin-right: 20px;
												margin-bottom: 20px;

												.md-block({
																float: none;
													});
										}

										&-title {
													font-size: 20px;
													margin-bottom: 12px;
										}

										&-text {
											font-size: 14px;
											margin-bottom: 16px;

											.sm-block({
															text-align: left;
												});
										}

										.actions {
													display: flex;
													flex-wrap: wrap;

													.sm-block({
																justify-content: center;
														});

													&-button  {
															width: 250px;
															margin-right: 20px;
															margin-bottom: 20px;
													}
										}

						}

			}


			.servicesAdd_2 {
						.row-flex;
						align-items: center;

					.servicesBottom {
								margin-bottom: 30px;
								.col;

								&-text {
									font-size: 18px;
									text-align: center;
									max-width: 550px;
									margin: 0 auto;

									.sm-block({
												max-width: 100%;
										});
								}

								&-img {
											.xs-block({
														display: none;
												});
								}

								&-btn  {
										width: 290px;
										margin-bottom: 5px;
								}

								&-download {
										text-decoration: underline;
								}
					}

					.servicesBottomLeft{
								display: flex;
								justify-content: space-between;
								.size(8);

								.lg-block({
											.size(12);
									});
					}

					.servicesBottomRight {
								display: flex;
								flex-direction: column;
								align-items: flex-end;
								.size(4);

								.lg-block({
											.size(12);
												align-items: center;
												margin-bottom: 0;
									});
					}

		 }
			
			
}



/* ======  About  ======= */

.about {
			background: url(../img/about/bg.png) center 2% no-repeat, #00C4DF;
			color: @white;

			.aboutWrap {
						margin-bottom: 30px;
						.row-flex;
						justify-content: center;
			}


			.aboutText {
						font-size: 20px;
						line-height: 26px;
						text-indent: 10px;

						.col;
						.size(7);

						.md-block({
										.size(11);
										margin-bottom: 50px;
							});
			}

			.aboutAdvantages {
						.col;
						.size(5);

						.md-block({
										.size(8);
							});

						.sm-block({
										.size(8);
							});

						.xs-block({
										.size(12);
							});

						.aboutAdvantagesWrap {
									.row-flex();
									justify-content: center;
						}

						.aboutAdvantage {
									position: relative;
									margin-bottom: 30px;
									
									.col;
									.size(6);

									.xs-block({
													.size(7);
										});

									.xs-block({
													.size(8);
										});

									&-img {
												position: relative;
												top: 50%;
												left: 50%;
												transform: translate(-50%; -50%);
									}

									&-text {
												position: absolute;
												top: 50%;
												left: 0;
												transform: translateY(-55%);
												max-width: 200px;
												font-size: 18px;
									}
						}
			}

}

	

/* ======  Advantages  ======= */

.advantages { 
			
			.advantagesWrap {
						.row-flex;
						justify-content: center;
			}

			.advantagesBlock {
					text-align: center;
					margin-bottom: 50px;
					.col();
					.size(4);

					.size-lg(6);

					.md-block({
								.size(8);
								margin-bottom: 30px;
						});

					.xs-block({
								.size(12);
						});
					
						&-img {
								
						}

						&-title {
							font-size: 36px;
							font-weight: 300;
							color: #008FA3;
							margin-bottom: 10px;
							position: relative;
							margin-top: 40px;
							padding-top: 20px;

							&::before {
								content: '';
								display: block;
								position: absolute;
								top: 0;
								left: 50%;
								transform: translateX(-50%);
								width: 190px;
								height: 1px;
								background: #008FA3;
							}
						}

						&-text {
							font-size: 18px;
							font-weight: 300;
							max-width: 320px;
							margin: 0 auto;
						}
			}

}
	


/* ======  Steps  ======= */
.steps {
			background: url(../img/steps/steps-bg.png) center center, #00C4DF;
			color: @white;

			.stepsWrap {
					.row-flex;
					justify-content: center;
			}

			.stepsBlock {
					margin-bottom: 30px;
					.col;
					.size(3);


					.lg-block({
								.size(6);
								margin-bottom: 30px;
						});

					.md-block({
								.size(8);
						});

					.sm-block({
								.size(10);
						});


						&-figure {
								font-size: 48px;
								font-weight: bold;
								position: relative;
								margin-bottom: 20px;

								&::after {
									content: "";
									display: block;
									width: 60px;
									height: 4px;
									background: @white;
									margin-top: 10px;
								}
						}

						&-text {
								font-size: 16px;
								line-height: 21px;
						}

			}

			
			.stepsForm {
						max-width: 80%;
						margin: 40px auto 40px auto;
						text-align: center;
						border: 5px solid @white;
						padding: 21px;

						&-title {
							font-size: 36px;
							margin-bottom: 15px;
						}

						&-subtitle {
								font-size: 18px;
								margin-bottom: 15px;
						}

						.stepsFormInputs {
									display: flex;
									flex-wrap: wrap;
									justify-content: center;

									.md-block({
												width: 150px;
												margin: 20px auto;
										});
						}

						.stepsFormInput {
									display: flex;
									align-items: center;
									margin-left: 30px;

									.md-block({
												flex-grow: 1;
												margin-bottom: 15px;
												width: 100%;
												margin-left: 0;
										});

									input {
										display: none;

										 	&:checked + label::before {
															background: url('../img/steps/ellips-icon.png') center center no-repeat;
												}
									}

									label {
										cursor: pointer;

										&::before {
													content: '';
													display: inline-block;
													width: 14px;
													height: 14px;
													margin-right: 5px;
													margin-top: -2px;
													border-radius: 50%;
													background: transparent;
													cursor: pointer;
													vertical-align: middle;
													border: 1px solid #FFFFFF;
													box-sizing: border-box;
										}
									}
						}
			}

			.stepsFormMain {
						.row-flex;
						margin-top: 50px;


						.md-block({
									margin-top: 0px;
							});

						.stepsFormMainInput {
									.col;
									.size(4);
									position: relative;

									.md-block({
													.size(8);
													margin: 0 auto 10px;
										});

									.sm-block({
													.size(10);
										});

									.xs-block({
													.size(12);
										});

									&-user {
										&::before {
											content: '';
											display: block;
											width: 20px;
											height: 24px;
											background: url(../img/steps/user-icon.png) no-repeat;
											position: absolute;
											top: 0;
											left: 0;
											margin: 15px 10px;
										}
									}

									&-phone {
										&::before {
											content: '';
											display: block;
											width: 24px;
											height: 24px;
											background: url(../img/steps/smartphone-icon.png) no-repeat;
											position: absolute;
											top: 0;
											left: 0;
											margin: 15px 10px;
										}
									}


									input[type="text"],
									input[type="tel"] {
											width: 100%;
											height: 50px;
											background: #F9F9F9;
											border: 0.5px solid #DBDBDB;
											box-sizing: border-box;
											border-radius: 2px;
											padding-left: 45px;
											font-size: 16px;
											color: #AFAFAF;

											&::-webkit-input-placeholder {
													font-size: 12px;
													color: #AFAFAF;
												}

											&::-moz-placeholder {
													font-size: 12px;
													color: #AFAFAF;
												}
									}

									.required {
											font-size: 12px;
											font-weight: 300;
											margin-top: 10px;
									}
						}
			}



.formCheck {
		text-align: left;
		margin-left: 10px;

		.md-block({
						text-align: center;
						margin: 20px auto 0;
			});
}


}



/* ======  Serve  ======= */

.serve {
			padding-bottom: 30px;
			
			.serveWrap {
				.row-flex;
				justify-content: center;
							
							.serveBlock {
										text-align: center;
										padding-bottom: 15px;
										margin-bottom: 40px;
										background: #FBFBFB;
										box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
										cursor: pointer;
										transition: all .8s;
										
										&:hover {
												transform: scale(1.1);
										}

										.col;
										.size(4);

										.lg-block({
													.size(6);
											});

										.md-block({
													.size(6);
											});

										.sm-block({
													.size(8);
											});

										.xs-block({
													.size(12);
											});

										&-link {
											color: @text;
										}

										&-img {
											margin-bottom: 15px;
										}
										&-title {
											font-size: 24px;
											padding: 5px;
											text-align: center;
											box-sizing: border-box;
										}


							}


			}

}




/* ======  Gratitude  ======= */

.gratitude {
			padding-top: 0;

			/* Слайдер */
			.gratitudeSlider {
							margin-bottom: 40px;

							.sliderItem {
								text-align: center;

								.md-block({
												img {
															width: 90%;
												}
									});

								.xs-block({
												img {
															width: 90%;
												}
									});
							}
			} 



			/* Рассчет стоимости */ 
			.gratitudeCalcWrap {
								background: @white;
								box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
						 	padding: 30px;

						 	.xs-block({
												padding: 0px;
									});


						 	.gratitudeCalc {
						 				border: 8px solid #00C4DF;
											box-sizing: border-box;
											padding: 50px 60px;

											.xs-block({
															padding: 50px 0px;
												});

											&-title {
													font-size: 32px;
													text-align: center;

													.md-block({
																	margin-bottom: 20px;
																	font-size: 26px;
														});
											}

											
											.gratitudeCalcBLock {
														.row-flex;
														align-items:center;
														justify-content: center;

														&-img {
															.col;
															.size(2);

															.size-md(3);
															.size-xs(6);
														}

														&-text {
															.col;
															.size(5);
															.size-md(9);
															.size-xs(12);

															font-size: 20px;
															font-style: italic;
															padding-left: 30px;
														}

														&-calc {
																.col;
																.size(5);

																.size-md(12);
																max-width: 234px;
																margin: 20px auto 0;
														}
											}


						 	}
			}


}



/* ======  FAQ  ======= */

.faq .section-title_lite h2 {
			
}

.faq {
		background: #00C4DF;

		.section-title_lite {
				text-align: center;
				position: relative;
					h2 {
							
							img {
										position: relative;
										top: 10px;
										margin-left: 20px;

										.sm-block({
													display: none;
											});
							}
					}


		}

	.faqWrap {
				.row-flex;
				justify-content: center;
	}

		.faqBlock {
					.col;
					.size(6);
					margin-bottom: 30px;

					.size-sm(10);
					.size-xs(12);

						&-question {
									background: #FFFFFF;
									box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
									border-radius: 5px 5px 0 0;
									padding: 20px 24px;
									font-size: 18px;
									cursor: pointer;

									.xs-block({
													font-size: 14px;
										});

									&-arrow {
											float: right;
											cursor: pointer;
											transform: rotate(0deg);
											transition: all .5s;

											&::after {
													content: '';
													display: block;
													clear: both;
											}

									}

									.activeQuestion {
													transform: rotate(180deg);
										}
						}

						&-answer {
								background: #FFFFFF;
								box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
								border-radius: 0 0 5px 5px;
								padding: 10px 24px 20px;
								font-size: 14px;
								line-height: 20px;
								display: none;
						}
		}


}



/* ======  contacts  ======= */

