				});

			.contactsWrap{
						.row-flex;
						justify-content: center;

						.contactsLeft {
									.col;
									.size(4);

									.lg-block({
													.size(3);
										});

									.md-block({
													.size(12);
										});
						}

						.contactsRight {
										.col;
										.size(8);

										.lg-block({
														.size(9);
											});

										.md-block({
													.size(12);
										});
						}


						.contactsBlock {
										margin-top: 20px;
										margin-bottom: 40px;

										.md-block({
														display: flex;
														flex-direction: column;
														align-items: center;
														margin-bottom: 20px;
											});
										
										&-title {
												font-size: 18px;
												font-weight: 300;
												margin-bottom: 5px;
