}



/* ======  footer  ======= */

.footer {
			background: #333;
			padding: 30px 0 0px;
			color: @white;

			a {
				color: @white;
			}

			.footerWrap {
					.row-flex;
					justify-content: center;
				}

				.footerLeft {

							.sm-block({
										.size(12);
										order: 2;
										display: flex;
										align-items: center;
										justify-content: space-around;
								});

							.sm-block({
										display: block;
										.size(10);
								});
								
				}

				.footerCenter {
							.sm-block({
										.size(6);
								});

							.xs-block({
										.size(10);
								});
				}

				.footerRight {
								.sm-block({
									.size(6);
							});
							.xs-block({
									.size(10);
							});
				}

			.footerBlock {

						.col;
						.size(4);


						margin-bottom: 30px;

						&-title {
							font-size: 12px;
							font-weight: 900;
							margin-bottom: 15px;
						}

						ul {
							li {
									margin-bottom: 15px;

									a {
										&:hover {
											text-decoration: underline;
										}
									}
							}
						}
			}


			.info {
					font-size: 12px;
					line-height: 17px;
					max-width: 170px;
					margin-top: 25px;

					a {
						display: block;
					}
			}


		.footerUnder {
					padding: 0 0 10px;
					opacity: 0.5;
					font-size: 10px;
					.row-flex;
					align-items: center;

					&::before {
							content: '';
							display: block;
							width: 97%;
							height: 1px;
							background: #fff;
							margin: 0 auto;
							margin-bottom: 10px;
					}

					&-copy {
							text-align: center;

							.xs-block({
										text-align: left;
								});
					}

					&-webbees {
							text-align: right;
					}


					.footerUnderBlock {
							.col;
							.size(4);

							.xs-block({
										.size(6);
								});
					}

					.footerUnder-none {
								
								.xs-block({
											display: none;
									});
					}
		}

			
}












/* Modals  */ 

.overlay {
		width: 100%;
		height: 100%;
		background: rgba(0,0,0, 0.6);

		position: fixed;
		top: 0;
		left: 0;
		z-index: 3000;
		cursor: pointer;
}

.modal {
		display: none;
		.sfreg();
		width: 700px;
		background: @white;
		border-radius: 5px;
		box-sizing: border-box;
		padding: 25px;
		text-align: center;

		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 3001;


		.md-block({
						width: 280px;
			});

		&-title {
				font-size: 24px;
				margin-bottom: 30px;

				.md-block({
								font-size: 20px;
								margin-bottom: 10px;
					});
		}

		&-subtitle {
				font-size: 14px;
				margin-bottom: 10px;

				.md-block({
								margin-bottom: 0;
					});
		}

		
		.modalFormOptions {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					max-width: 500px;
					margin: 20px auto;

					.md-block({
								display: block;
								max-width: 150px;
								margin: 10px auto 20px;
								text-align: left;
						});

					.modalFormOption {

							input {
								display: none;

								 	&:checked + label::before {
													background: url('../img/modals/ellipse-form.png') center center no-repeat;
													border-color: #008FA3;
										}
							}

							label {
								cursor: pointer;

								&::before {
											content: '';
											display: inline-block;
											width: 10px;
											height: 10px;
											margin-right: 5px;
											margin-top: -2px;
											border-radius: 50%;
											background: transparent;
											cursor: pointer;
											vertical-align: middle;
											border: 1px solid #333;
											box-sizing: border-box;
								}
							}

					}
		}

		.modalFormInput  {
					width: 50%;
					height: 45px;
					margin: 0 auto 20px;

					.md-block({
									width: 100%;
									margin-bottom: 10px;
						});

						&-user {
							position: relative;
								&::before {
										content: '';
										display: block;
										width: 20px;
										height: 24px;
										background: url(../img/modals/user-icon.png) no-repeat;
										margin: 10px 0 0 10px;
										position: absolute;
										top: 0;
										left: 0;
								}
						}

						&-phone {
							position: relative;
								&::before {
										content: '';
										display: block;
										width: 24px;
										height: 24px;
										background: url(../img/modals/smartphone-icon.png) no-repeat;
										margin: 10px 0 0 10px;
										position: absolute;
										top: 0;
										left: 0;
								}
						}

						
						
						input[type="text"],
						input[type="tel"] {
								width: 100%;
								height: 100%;
								box-sizing: border-box;
								padding-left: 40px;
								font-size: 16px;
								color: #AFAFAF;

								background: #F9F9F9;
								border: 0.5px solid #DBDBDB;
								border-radius: 2px;

								&::-webkit-input-placeholder {
										font-size: 12px;
										color: #AFAFAF;
									}

								&::-moz-placeholder {
										font-size: 12px;
										color: #AFAFAF;
									}
						}

						.required {
								font-size: 10px;
								color: #AFAFAF;
								margin-top: 10px;
						}
						


		}


.formCheck {
			margin-bottom: 30px;

		&-input {
			 	&:checked + label::before {
								background: url('../img/modals/checkForm-icon.png') center center no-repeat;
					}
		}

		label {
			opacity: .8;
		}

}



	&-close {
			position: absolute;
			top: 0;
			right: 0;
			margin: 15px;
			opacity: .7;
			cursor: pointer;
	}


}



