/* Даю согласие */

.formCheck {
  margin-top: 20px;
  margin-left: -25px;
}

.formCheck-input {
  display: none;
}

.formCheck-input:checked + label::before {
  background: url('../img/main/check-icon.png') center center no-repeat;
}

.formCheck label {
  padding-left: 5px;
  font-size: 12px;
}

.formCheck label:before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  border: .5px solid #DBDBDB;
  border-radius: 2px;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin-top: -3px;
}

