// main: ../style.less


// Default Variables

@slick-font-path: "./fonts/";
@slick-font-family: "slick";
@slick-loader-path: "./";
@slick-arrow-color: white;
@slick-dot-color: black;
@slick-dot-color-active: @slick-dot-color;
@slick-prev-character: "←";
@slick-next-character: "→";
@slick-dot-character: "•";
@slick-dot-size: 6px;
@slick-opacity-default: 0.75;
@slick-opacity-on-hover: 1;
@slick-opacity-not-active: 0.25;


/* Arrows */


.gratitude {

    .slick-prev,
    .slick-next {
        border: 0;
        color: transparent;
        font-size: 0;

        width: 27px;
        height: 82px;
        background: url("../img/gratitude/slider-arrow.png") no-repeat;

        position: absolute;
        top: 50%;
        z-index: 1;
        transform: translateY(-50%);
        opacity: .8;

        &:focus {
            outline: 0;
        }

        &:hover {
            opacity: 1;
            cursor: pointer;
        }
    }

    .slick-prev {
        left: -30px;
    }

    .slick-next {
        right: -30px;
        background-position: -27px 0;
    }

}



.sliderMain {
    
    .slick-prev,
    .slick-next {
        color: transparent;
        font-size: 0;
        border: 0;
        width: 15px;
        height: 34px;
        background: url("../img/main/sliderArrow.png") no-repeat;

        position: absolute;
        bottom: 80px;
        left: 70px;
        z-index: 1;
        transform: translateY(-50%);
        opacity: .8;



        &:focus {
            outline: 0;
        }

        &:hover {
            opacity: 1;
            cursor: pointer;
        }

        &::before {
            content: '';
            display: block;
            width: 49px;
            height: 49px;
            margin-left: -25px;
            margin-top: -10px;
            border: 1px solid #FFFFFF;
            box-sizing: border-box;
            box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
        }
    }

    .slick-prev {

    }

    .slick-next {
        left: 140px;
        background-position: -15px 0;
    }


}



/* Dots */

.gratitude {

    .slick-dots {
        position: absolute;
        bottom: -20px;
        list-style: none;
        display: block;
        text-align: center;
        padding: 0;
        width: 100%;

        li {
            position: relative;
            display: inline-block;
            margin: 0 10px;
            padding: 0;
            cursor: pointer;

            button {
                padding: 0;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                border: 1px solid #00C4DF;
                box-sizing: border-box;            
                background: #fff;
                color: transparent;
                font-size: 0;

                &:hover {
                    cursor: pointer;
                    opacity: 1;
                }

                &:hover, &:focus {
                    outline: none;
                }
            }

            &.slick-active button {
                background-color: #00C4DF;
                opacity: 1;
                position: relative;
                
                &::after {
                    content: '';
                    display: block;
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    border: 1px solid #00C4DF;;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }

            }

            


        }
    }

    .slick-slider {
        margin-bottom: 30px;
    }
}


.sliderMain {

    .slick-dots {
        position: absolute;
        bottom: 50px;
        left: 50px;
        list-style: none;
        display: block;
        text-align: left;
        padding: 0;
        max-width: 50%;

        li {
            position: relative;
            display: inline-block;
            margin: 0 10px;
            padding: 0;
            cursor: pointer;

            button {
                padding: 0;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                border: 1px solid #fff;
                box-sizing: border-box;            
                background: transparent;
                color: transparent;
                font-size: 0;

                &:hover {
                    cursor: pointer;
                    opacity: 1;
                }

                &:hover, &:focus {
                    outline: none;
                }
            }

            &.slick-active button {
                background-color: #fff;
                opacity: 1;
                position: relative;
                
                &::after {
                    content: '';
                    display: block;
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    border: 1px solid #fff;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }

            }

            


        }
    }
}



/* Slider */

.slick-slider {
    position: relative;
    display: block;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;

    &:focus {
        outline: 0;
    }
    img {
        display: inline-block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}
